<template>
  <multiselect
    v-model="selectedHero"
    track-by="name_en"
    label="name_en"
    placeholder="Select one"
    selectLabel=""
    deselectLabel="Can't remove this value"
    :value="selectedHero"
    :options="heroes"
    :searchable="true"
    :allow-empty="false"
    :clear-on-select="false"
    :maxHeight="480"
  >
    <template v-if="selectedHero.id" slot="singleLabel" slot-scope="{ option }">
      <div class="selected">
        <Portrait
          path="/images/heroes_portraits/small"
          :file="`${selectedHero.name_ru}`"
        />
        <span class="multiselect__selected">
          {{ option.name_ru }}
        </span>
      </div>
    </template>

    <template slot="option" slot-scope="props">
      <div class="options">
        <Portrait
          path="/images/heroes_portraits/small"
          :file="`${props.option.name_ru}`"
        />
        <span>{{ props.option.name_ru }}</span>
      </div>
    </template>
  </multiselect>
</template>

<script>
import Multiselect from "vue-multiselect";

import "vue-multiselect/dist/vue-multiselect.min.css";

export default {
  components: {
    Multiselect,
    Portrait: () => import("@/components/damageCalculator/Portrait.vue")
  },
  props: {
    heroes: {
      value: Array,
      default: [],
      required: false
    }
  },
  data() {
    return {
      selectedHero: {}
    };
  },
  methods: {
    onSelect() {
      this.$emit("select", this.selectedHero);
    }
  }
};
</script>

<style lang="scss">
.multiselect {
  min-height: 48px;
}

.selected,
.options {
  display: flex;
  align-items: center;
  font-size: 1.125rem;

  img {
    margin-right: 10px;
  }
}
</style>
